.users {
  margin: 2rem auto;
  padding: 1rem;
  width: 50%;
  max-width: 40rem;
  background: rgb(255, 255, 255, 0.5);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 10px;
  padding: 3%;
}
@media (max-width: 800px) {
  .users {
    width: 90%;
  }
}

.users label {
  display: block;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.users users {
  font: inherit;
  display: block;
  width: 100%;
  border: 1px solid #ccc;
  padding: 0.15rem;
  margin-bottom: 0.5rem;
}

.users users:focus {
  outline: none;
  border-color: #4f005f;
}